.top-icon {
    width: auto !important;
    margin: auto !important;
}

.zstack {
    display: grid;
    align-items: center;
    justify-items: center;
}

.zstack>* {
    grid-area: 1/1/1/1;
}


li > a,
.nav-link-active,
.link {
    color: #2C6030 !important;
}

.vr {
    color: #2C6030 !important;
    width: 1.60px;
}

.opac {
    opacity: 1 !important;
}

.zic {
    color: #2C6030 !important;
    /* opacity: 0.3; */
    margin-bottom: -1px;
    margin-top: -1px;
}

.ml-100 {
    margin-left: 150px;
    width: 100px;
    text-align: left;
}

.mb-50 {
    margin-bottom: 50px;
}

.version-number {
    font-size: 15px;
    opacity: 0.5;
    font-weight: bolder;
}

.profile {
    border: 2px solid #ddd;
    margin: 5px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: fill;
    transform: scale(1);
    animation: profile-scale-animate-down 1s ease-in-out;
  }
  
  .profile:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transform: scale(1.2);
    border: 2px solid #ddd;

    animation: profile-scale-animate 1s ease-in-out;
  }

  .legende {
    font-size: 10px;
    text-decoration: none;
    color: #2c603069;
    font-weight: bold;
  }

@media only screen and (min-width: 768px) {
    .top-icon {
        width: 700px !important;
        margin: auto !important;
    }
}


@keyframes profile-scale-animate {
    0% {
        transform: scale(1.0);
    }
    100% {
        transform: scale(1.2);
    }
}

@keyframes profile-scale-animate-down {
    0% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}

